@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Display&display=swap);
#calculator {
  width: 425px;
  height: 570px;
  background-color: var(--primary-color-darker);
  box-shadow: 5px 7px 30px rgba(30, 30, 30, 0.2);
  border-radius: 3px;
  overflow: hidden;
}

#display {
  width: 100%;
  height: 110px;
  padding: 30px 20px;
  align-items: flex-end;
  justify-content: center;
  font-weight: bold;
  word-wrap: break-word;
  word-break: break-all;
  color: var(--text-color-light);
  background-color: var(--text-color);
}

#prev-operand {
  color: var(--secondary-color);
  font-size: 20px;
}

#current-operand {
  font-size: 32px;
}

#buttons-container {
  display: grid;
  flex: 1 1;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 3px;
  gap: 3px;
  padding: 3px;
}

.button {
  display: grid;
  place-content: center;
  background-color: var(--primary-color);
  font-weight: bold;
  font-size: 20px;
  transition: 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  outline: none;
}

.button:hover {
  background-color: var(--accent-color);
  color: var(--text-color-light);
}

.operation {
  background-color: var(--secondary-color);
}

#clear {
  grid-column: span 2;
  background-color: #e72b5a;
  color: var(--text-color-light);
}

#clear:hover {
  background-color: var(--accent-color);
}

#equals {
  grid-row: span 2;
}

#zero {
  grid-column: span 2;
}

:root {
  --primary-color: #f7ece1;
  --secondary-color: #829cbc;
  --primary-color-darker: #eee5dd;
  --accent-color: #6290c8;
  --text-color: #393649;
  --text-color-light: var(--primary-color);
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.centered {
  justify-content: center;
  align-items: center;
}

.App {
  background-color: #829cbc;
  background-color: var(--secondary-color);
  font-family: "Noto Sans Display", sans-serif;
  color: #393649;
  color: var(--text-color);
}

main {
  width: 100%;
  height: 100vh;
}

